import React, { useEffect } from "react";
import { Row, Col, Image, Card } from "antd";
import "./index.scss"
export default function ImageType(props) {
  const { data = [] } = props;
  useEffect(() => {}, [data]);
  return (
    <div className="layout-styles ImageType">
      <Row gutter={[0, 0]}>
        {data.map((item, index) => {
          return (
            <Col span={8}>
              <Card
                className="header-solid h-full"
                bordered={false}
                title={[
                  <h6 className="font-semibold m-0">
                    {item.type.toUpperCase()}
                  </h6>,
                ]}
              >
                <Row gutter={[0, 12]}>
                  {item?.urls?.map((i) => {
                    return (
                      <Col span={24}>
                        <Image
                          width={200}
                          height={200}
                          src={i}
                          fallback="data:image/png;base64,"
                        />
                      </Col>
                    );
                  })}
                </Row>
              </Card>
            </Col>
          );
        })}
      </Row>
    </div>
  );
}
